import React,{useEffect, useState} from 'react';
import {Link} from "react-router-dom"
import {Row,Col,Image,ListGroup,Button,Card,Form} from "react-bootstrap"
import Rating from "../components/Rating"
import {listProductDetails} from '../actions/productActions'
import {useDispatch,useSelector} from 'react-redux'
import Loading from '../components/Loader'
import Message from '../components/Message'
function ProductScreen({match,history}) {
   const dispatch = useDispatch();
    useEffect(()=>{
        dispatch(listProductDetails(match.params.id))
    },[dispatch,match])
    const addToCardHandaler =()=>{
            history.push(`/card/${match.params.id}?qtn=${qtn}`)
    }
  const productDetails= useSelector(state=>state.productDetails)
    const {loading,error,product}=productDetails;
    const [qtn,setQtn]= useState(1)
    return (
        <div>
          <Link to="/" className="btn btn-light my-3">Go back</Link>
          {loading?<Loading/>
            :error ? <Message variant='danger'>{error}</Message>
            :  (<Row>
              <Col md={6}>
                <Image src={product.image} alt={product.name} fluid/>
              </Col>
               <Col md={3}>
                    <ListGroup variant="flush">
                            <ListGroup.Item>
                                <h3>{product.name}</h3>
                            </ListGroup.Item>
                            <ListGroup.Item>
                                <Rating value={product.rating} text={`${product.numReviews} reviews`} color={'#f8e825'}/>
                            </ListGroup.Item>
                            <ListGroup.Item>
                                Price: ${product.price}
                            </ListGroup.Item>
                             <ListGroup.Item>
                                Description: {product.description}
                            </ListGroup.Item>
                    </ListGroup>
              </Col>
               <Col md={3}>
                        <Card>
                            <ListGroup variant="flush">
                                <ListGroup.Item>
                                    <Row>
                                        <Col> Price :</Col>
                                        <Col><strong>${product.price}</strong> </Col>
                                    </Row>
                                </ListGroup.Item>
                                <ListGroup.Item>
                                    <Row>
                                        <Col> Status :</Col>
                                        <Col><strong>{product.countInStock > 0 ? "In stock" : "Out of Stock"}</strong> </Col>
                                    </Row>
                                </ListGroup.Item>
                                {product.countInStock > 0 && (
                                    <ListGroup.Item>
                                        <Row>
                                            <Col >Qtn : </Col>
                                            <Col xs='auto' className="my-1">
                                                <Form.Control
                                                as="select"
                                                value={qtn}
                                                onChange={(e)=>setQtn(e.target.value)}
                                                >
                                                        {[...Array(product.countInStock).keys()].map((x)=>(
                                                            <option key={x+1} value={x+1}>
                                                                {x+1}
                                                            </option>
                                                        ))}
                                                </Form.Control>
                                            </Col>
                                        </Row>
                                    </ListGroup.Item>
                                )}
                                <ListGroup.Item>
                                    <Button onClick={addToCardHandaler} className="btn-block" disabled={product.countInStock === 0 } type="button">ADD TO CART</Button>
                                </ListGroup.Item>
                            </ListGroup>
                        </Card>
              </Col>
          </Row>)
        }
        
        </div>
    );
}

export default ProductScreen;